* {
  box-sizing: border-box;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

body {
  flex-direction: column;
  justify-content: end;
  align-items: center;
  width: 100vw;
  height: 100vh;
  padding-bottom: 50px;
  display: flex;
}

audio {
  opacity: .2;
  transition: all .5s linear;
}

audio:hover {
  opacity: .8;
}

canvas {
  z-index: -1;
  border: 0;
  width: 100%;
  height: 100%;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

